/**
 * WordPress dependencies
 */
const {	RichText } = wp.blockEditor;
const { Path, SVG } = wp.components;

export default function save( {
	attributes,
	className,
} ) {
	const {
		recordedVideoUrl,
		recordedVideoLink,
	} = attributes;

	if ( ! recordedVideoUrl ) {
		return null;
	}

	return (
		<p className={ className }>
			<SVG
				className={ 'svg-icon' }
				viewBox="0 0 24 24"
				width="24"
				height="24"
				xmlns="http://www.w3.org/2000/svg"
			>
				<Path fill="none" d="M0 0h24v24H0V0z" />
				<Path d="M4 6.47L5.76 10H20v8H4V6.47M22 4h-4l2 4h-3l-2-4h-2l2 4h-3l-2-4H8l2 4H7L5 4H4c-1.1 0-1.99.9-1.99 2L2 18c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V4z" />
			</SVG>
			<RichText.Content
				tagName="a"
				className={ 'course-recorded-video__link' }
				href={ recordedVideoUrl }
				value={ recordedVideoLink }
			/>
		</p>
	);
}
